.nav-wrapper{
    width: 100%;
    height: 85px;
    min-width: 1242px;
}
.nav{
    display: flex;
    width: 1170px;
    margin: 0 auto; 
    font-size: 14px;
    height: 85px;
}
.nav-list{
    display: flex;
    list-style-type: none;
    margin: 61px 0 0 133px;
    padding: 0;
}
.menu-burger{
    display: none;
    height: 22px;
    margin: 37px 32px 0 0;
}
.menu-basket{
    margin: 55px 7px 0 0;
    height: 26px;
}
header{
    display: flex;
    margin: 0 auto;
    width: 1242px;
    padding: 0 36px 0 36px;
    height: 468px;
}
.header-box{
    display: flex;
}
.header-title{
    width: 570px;
    padding-top: 134px;
}
.header-title-card{
    display: flex;
    flex-grow: 1;
    margin: 49px 0 0 130px; 
    flex-direction: column;
}
main{
    width: 1242px;
    margin: 71px auto 0 auto;
}
.list-wrapper{
    display: flex;
}
.visible-list{
    width: 1170px;
    overflow: hidden;
    margin: 0 15px 0 15px;
}
.special-wrapper{
    display: flex;
    justify-content: space-between;
}
@media only screen and (max-width: 1242px) {
    .nav-wrapper{
        min-width: 768px;
    }
    .nav{
        width: 706px;
    }
    .nav-list{
        display: none;
    }
    .nav h6{
        margin-top: 32px !important;
    }
    .menu-basket{
        margin: 33px 12px 0px 0px !important;
    }
    .menu-burger{
        display: block;
    }
    .nav div:nth-child(3){
        line-height: 90px !important;
    }
    header{
        /* flex-direction: column; */
        height: 760px;
        width: 768px !important;
        padding: 0 !important;
    }
    .header-box{
        flex-direction: column;
        margin: 0 auto;
        border-bottom: 1px solid #745F4C;
    }
    .header-title{
        width: 734px;
        margin: 0 auto;
        padding-top: 37px;
    }
    .header-title br{
        display: none;
    }
    .header-title h2{
        text-align: center;
        margin-bottom: 12px !important;
        width: 482px;
        margin: 0 auto 0 auto;
    }
    .header-title p{
        margin: 0 auto 15px auto !important;
        width: 482px !important;
        text-align: center;
    }
    .header-title hr{
        display: none;
    }
    .header-title button{
        margin:  0 0 46px 251px;
    }
    .header-title-card{
        margin: 5px auto 0 auto;
    }
    main{
        width: 768px;
        margin: 65px auto 0 auto;
    }
    main h1{
        width: 768px !important;
    }
    .list-wrapper{
        width: 646px;
        margin:0 auto;
    }
    article{
        width: 768px !important;
        margin: 58px auto 0px auto !important;
    }
    article h1{
        margin-bottom: 43px !important;
    }
    .special-wrapper{
        flex-direction: column;
    }
    .special-wrapper div:nth-child(1){
        margin: 0 auto 50px auto;
    }
    .special-wrapper div:nth-child(2){
        margin: 0 auto;
    }
    footer{
        min-width: 734px !important;
        margin-top: 100px !important;

    }
    footer div{
        display: none;
    }
}
@media only screen and (max-width: 768px) {
    .nav b{
        display: none;
    }
    .nav-wrapper{
        min-width: 320px;
    }
    .nav{
        width: 290px;
    }
    .menu-basket{
        margin-right: 8px !important;
        margin-top: 30px !important;
    }
    .menu-burger{
        margin: 32px 27px 0 0;
    }
    .nav h6{
        margin-top: 27px!important;
    }
    header{
        width: 320px !important;
        height: 648px !important;
    }
    .header-title{
        width: 290px;
        padding-top: 18px;
    }
    .header-title h2{
        width: 290px;
        font-size: 25px !important;
        text-align: left;
        margin-bottom: 6px !important;
    }
    .header-title p{
        font-size: 14px !important;
        width: 290px !important;
        text-align: left;
        line-height: 18px !important;
    }
    .header-title button{
        width: 163px !important;
        font-size: 14px !important;
        margin-left: -3px;
    }
    .header-title-card{
        margin-top: 0px;
    }
    .header-title-card div{
        width: 290px !important;
        height: 290px !important;
    }
    .header-title-card button:nth-child(1){
        margin-top: 310px !important;
    }
    main{
        width: 320px;
        margin-top: 35px;
    }
    main h1{
        width: 320px !important;
        margin-bottom: 20px !important;
        font-size: 20px !important;   
    }
    .list-wrapper{
        width: 320px;
    }
    .visible-list{
        margin: 0 2px;
    }
    .item{
        height: 363px !important;
    }
    article{
        padding: 0 !important;
        width: 320px !important;
        margin-top: 26px !important;
    }
    article h1{
        width: 320px !important;
        font-size: 19px !important;
        margin-bottom: 19px !important;
    }
    .special-wrapper div{
        width: 290px !important;
        height: 287px !important;
    }
    .special-wrapper div:nth-child(1){
        margin-bottom: 15px;
    }
    .special-wrapper button{
        width: 79px !important;
        height: 28px !important;
        font-size: 12px !important;
        margin: 248px 0 0 200px !important;
    }
    footer{
        height: 166px !important;
        min-width: 320px!important;
    }
}